			<text> 习近平总书记对垃圾分类做出重要指示，强调实行垃圾分类，关系广大人民群众生活环境，关系节约使用资源，也是社会文明水平的一个重要体现。党员干部要争做垃圾分类的先行者，以身作则，以上率下，带好头，出实招、创新招、使高招，做好垃圾分类这篇“文章”。

　　随着时代的发展，垃圾分类刻不容缓。有三座“垃圾大山”给许多城市造成了巨大的压力。“一山”叫垃圾速度增长过快：外卖过度包装、一次性餐具滥用；有的人日常生活讲养生不讲节约，剩菜不能，做饭一堆。“二山”叫做垃圾处理厂发展速度跟不上：扩建没地方，自身消化负荷超载。“三山”叫做垃圾围城：新型垃圾种类多、占地大。面对这三座大山，党员干部用“三招”破解垃圾处理难题。

　　干部带头齐抓共管，营造全面参与氛围。“我是党员，我带头”，各级党组织高度重视，强化组织领导，真正做到“一把手”亲自抓，层层抓落实，强化组织调度，督促第三方服务单位、物业企业到岗到位，履职尽责。基层党组织要积极发挥垃圾分类管理主体作用，高度重视垃圾分类工作，将垃圾分类作为“一把手工程”，做到亲自过问、亲自部署、亲自检查；带头实施垃圾分类，组织属地城管队、食药所等相关部门，对辖区内居住区和公共机构开展垃圾分类督导检查，共同营造全民参与垃圾分类的良好局面。

　　主动引导群众参与，探索垃圾分类模式。党员干部通过创新方式，寓教于乐，引导群众主动参与垃圾分类。一些地方通过绘制高颜值彩绘墙，“靓”出垃圾分类风景线；或是将“垃圾分类”元素融入剪纸，让居民通过剪纸艺术更直观了解垃圾分类。垃圾变废为宝，小妙招让垃圾分类逐渐深入人心。依法管理政策保障，健全分类长效机制。习近平总书记曾强调，要加快建立分类投放、分类收集、分类运输、分类处理的垃圾处理系统，形成以法治为基础、政府推动、全民参与、城乡统筹、因地制宜的垃圾分类制度。随着新修订的《北京市生活垃圾管理条例》的实施，各级党委要加大推进力度，把垃圾分类纳入地区党建工作内容，形成党建引领、居民自治、居委协调、物业参与的工作格局；成立垃圾分类工作领导小组，完善制度规范、协调部门联动、深入社区基层，通过人性化服务带动精细化管理，提高垃圾分类的社会参与度和正确投放率，着力促进垃圾分类长效机制建设。

　　垃圾分类难度不可小觑，不会一蹴而就，需要全体党员干部拧成一股绳，同心干，引导群众做改善环境的先行者，做保护环境的践行者，大力推进生活垃圾分类，让垃圾分类成为一种习惯，让文明投放垃圾成为一种素养。</text>
